<script>
  import { onMount } from "svelte";
  import parallax from "./parallax";

  function startParallax() {
    let $ = document.querySelector.bind(document);

    let blob1 = $("#blob-1");
    let blob2 = $("#blob-2");
    let blob3 = $("#blob-3");
    let blob4 = $("#blob-4");

    parallax(
      (lastScrollY, prefix, pos, setTickingFalse) => {
        let relativeY = lastScrollY / 4000;
        let xmultiplier = window.innerWidth / 960;
        // big blue top corner
        prefix(
          blob1.style,
          "Transform",
          "translate3d(" +
            ((960 * xmultiplier).toFixed() - blob1.width).toFixed() +
            "px," +
            pos(-100, -200, relativeY, 0) +
            "px, 0)"
        );
        // big red
        prefix(
          blob2.style,
          "Transform",
          "translate3d(" +
            300 * xmultiplier +
            "px," +
            pos(604, -140, relativeY, 0) +
            "px, 0)"
        );

        // first small blue
        prefix(
          blob3.style,
          "Transform",
          "translate3d(" +
            268 * xmultiplier +
            "px," +
            pos(44, -500, relativeY, 0) +
            "px, 0)"
        );

        // service 1
        prefix(
          blob4.style,
          "Transform",
          "translate3d(" +
            ((902 * xmultiplier).toFixed() - blob4.width).toFixed() +
            "px," +
            pos(1904, -2000, relativeY, 0) +
            "px, 0)"
        );
        setTickingFalse();
      },
      function() {
        blob1.classList.add("force-show-sm");
        blob2.classList.add("force-show");
        blob3.classList.add("force-show");
        blob4.classList.add("force-show");
      },
      startParallax
    );
  }
  onMount(async () => {
    startParallax();
  });

  import Nav from "../components/Nav.svelte";
  import Footer from "../components/Footer.svelte";
</script>

<svelte:head>
  <title>Lagarsoft - Services</title>
  <meta name="description" content="Lagarsoft - Staff Services " />

</svelte:head>
<Nav />
<container
  class="block ml-12 sm:ml-24 w-3/5 text-lgsftDarkBlue leading-none
  font-hairline mb-24 md:mb-36 lg:mb-48 sm:mt-24 md:mt-36 lg:mt-12 z-30">
  <h1
    class="text-3xl sm:inline-block sm:text-4xl md:text-5xl lg:text-6xl
    max-w-4xl">
    Staff Augmentation 
  </h1>
  <br />
  <a
    class="inline-block bg-lgsftDarkBlue hover:bg-lgsftRed bg-color-ease text-white pb-2 pt-1
    pl-4 pr-8 rounded-full tracking-wider my-8 mr-auto"
    href="/about-us">
    <span class="align-text-top leading-relaxed font-hairline ">about us</span>
  </a>
</container>
<container
  class="bg-lgsftDarkBlue flex items-center justify-between flex-wrap py-8 z-30">
  <container
    class="mx-auto text-white text-center ">
    <p
      class="mx-auto tracking-normal w-64 sm:w-1/2  max-w-6xl font-hairline
      ">
      We are a team of engineers with a background in software development,  data analysis, automation, architecture, real estate,  and agile management.
    </p>
  </container>
</container>
<container class="bg-white w-full inline-block">
  <container class="block bg-no-repeat">
    <container
      class="py-24 sm:py-16 block mx-auto w-4/5 sm:w-2/3 sm:mr-auto sm:mb-24
      sm:my-12 text-lgsftDarkBlue leading-none relative z-30">

      <p
        class="mt- sm:mr-auto sm:w-3/4 text-lgsftDarkBlue font-thin
        tracking-normal leading-tight">
        <span
          class="text-2xl inline-block tracking-wide w-full font-thin">
          Service Summary
        </span>
        <br />
        <br />
        Hiring highly specialized staff members is a difficult, time consuming
        and costly task, that may not be aligned to the changing needs of a
        project. Being able to ramp up or down team size according to meet
        changing demand without dealing with the liabilities of additional
        full-time employees can be a game-changer strategy.
        <br />
        <br />
        We had been leveraging teams of Architects, Designers, BIM Managers,
        Construction Managers and Lawyers since 2014, providing technical
        expertise and helping avoid technological hype. When an active project
        requires more resources to be completed on time, staff augmentation is
        typically the best option and is often the only option.
        <br />
        <br />
        Startups and mid-size companies usually require people to do too many
        tasks, that cause overhead, context switching, and underperformance; By
        adding new skill sets to the team, a company can take advantage of both
        external and internal resources, allowing people to do what they love
        and do best.
        <br />

      </p>
    </container>
  </container>

  <div id="blob-container">
    <img class="z-20 w-2/5 max-w-lg" alt="" id="blob-1" src="img/vector2.svg" />
    <div
      class=""
      alt=""
      id="blob-2"
      style="background-color: rgb(231, 70, 44); border-radius: 50%; width: 60%;
      padding-top:60%; z-index: -1;" />
    <img class="z-20 w-1/5" alt="" id="blob-3" src="img/vector1.svg" />
    <img class="z-10 w-2/7 max-w-sm opacity-25 sm:opacity-100" alt="" id="blob-4" src="img/vector7.svg" />

    <div class="force-show" />

  </div>
</container>

<Footer />
